.gallery {
  margin-left: -10px !important;
  margin-right: -10px !important;
}

.gallery-item {
  padding: 10px;

  .img-container {
    position: relative;
    height: 0;
    /* .gallery-item have a aspect ratio of 3:2*/
    padding-bottom: calc(100% * (2 / 3));

    img {
      /*covers the parent element completely, no matter which size it has*/
      position: absolute;
      width: 100%;
      height: 100%;

      object-fit: cover;
      object-position: top;
      border-radius: 4px;

      /*1 dp shadow in material design*/
      box-shadow: 0px 2px 1px -1px rgba(0, 0, 0, 0.2),
        0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 1px 3px 0px rgba(0, 0, 0, 0.12);
      transition: box-shadow 280ms cubic-bezier(0.4, 0, 0.2, 1),
        transform 280ms ease-in-out;

      &:hover {
        transform: scale(1.05);
        /*8 dp shadow in material design*/
        box-shadow: 0px 5px 5px -3px rgba(0, 0, 0, 0.2),
          0px 8px 10px 1px rgba(0, 0, 0, 0.14),
          0px 3px 14px 2px rgba(0, 0, 0, 0.12);
      }
    }
  }
}

/*Override PhotoSwipe top bar opacity*/
.pswp__ui--fit .pswp__top-bar,
.pswp__top-bar {
  background-color: rgba(0, 0, 0, 0);
}

/*Add 24dp shadow to lightbox image (same as material dialog)
  CSS from https://material-components.github.io/material-components-web-catalog/#/component/elevation */
.pswp--zoom-allowed .pswp__img,
.pswp__img {
  box-shadow: 0 11px 15px -7px rgba(0, 0, 0, 0.2),
    0 24px 38px 3px rgba(0, 0, 0, 0.14), 0 9px 46px 8px rgba(0, 0, 0, 0.12);
}
